
.playlist {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 200;
  background-color: rgba(0, 0, 0, 0.3);
}
.playlist.list-fade-enter-active, .playlist.list-fade-leave-active {
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s;
}
.playlist.list-fade-enter-active .list-wrapper, .playlist.list-fade-leave-active .list-wrapper {
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
}
.playlist.list-fade-enter, .playlist.list-fade-leave-to {
    opacity: 0;
}
.playlist.list-fade-enter .list-wrapper, .playlist.list-fade-leave-to .list-wrapper {
      -webkit-transform: translate3d(0, 100%, 0);
              transform: translate3d(0, 100%, 0);
}
.playlist .list-wrapper {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #333;
}
.playlist .list-wrapper .list-header {
      position: relative;
      padding: 20px 30px 10px 20px;
}
.playlist .list-wrapper .list-header .title {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.playlist .list-wrapper .list-header .title .icon {
          margin-right: 10px;
          font-size: 30px;
          color: rgba(255, 205, 49, 0.5);
}
.playlist .list-wrapper .list-header .title .text {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.5);
}
.playlist .list-wrapper .list-header .title .clear {
          position: relative;
}
.playlist .list-wrapper .list-header .title .clear:before {
            content: '';
            position: absolute;
            top: -10px;
            left: -10px;
            right: -10px;
            bottom: -10px;
}
.playlist .list-wrapper .list-header .title .clear .icon-clear {
            font-size: 15px;
            color: rgba(255, 255, 255, 0.3);
}
.playlist .list-wrapper .list-content {
      max-height: 240px;
      overflow: hidden;
}
.playlist .list-wrapper .list-content .item {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        height: 40px;
        padding: 0 30px 0 20px;
        overflow: hidden;
}
.playlist .list-wrapper .list-content .item.list-enter-active, .playlist .list-wrapper .list-content .item.list-leave-active {
          -webkit-transition: all 0.1s;
          transition: all 0.1s;
}
.playlist .list-wrapper .list-content .item.list-enter, .playlist .list-wrapper .list-content .item.list-leave-to {
          height: 0;
}
.playlist .list-wrapper .list-content .item .current {
          -webkit-box-flex: 0;
              -ms-flex: 0 0 20px;
                  flex: 0 0 20px;
          width: 20px;
          font-size: 12px;
          color: rgba(255, 205, 49, 0.5);
}
.playlist .list-wrapper .list-content .item .text {
          -webkit-box-flex: 1;
              -ms-flex: 1;
                  flex: 1;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          font-size: 14px;
          color: rgba(255, 255, 255, 0.3);
}
.playlist .list-wrapper .list-content .item .like {
          position: relative;
          margin-right: 15px;
          font-size: 12px;
          color: #ffcd32;
}
.playlist .list-wrapper .list-content .item .like:before {
            content: '';
            position: absolute;
            top: -10px;
            left: -10px;
            right: -10px;
            bottom: -10px;
}
.playlist .list-wrapper .list-content .item .like .icon-favorite {
            color: #d93f30;
}
.playlist .list-wrapper .list-content .item .delete {
          position: relative;
          font-size: 12px;
          color: #ffcd32;
}
.playlist .list-wrapper .list-content .item .delete:before {
            content: '';
            position: absolute;
            top: -10px;
            left: -10px;
            right: -10px;
            bottom: -10px;
}
.playlist .list-wrapper .list-operate {
      width: 140px;
      margin: 20px auto 30px auto;
}
.playlist .list-wrapper .list-operate .add {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        padding: 8px 16px;
        border: 1px solid rgba(255, 255, 255, 0.5);
        border-radius: 100px;
        color: rgba(255, 255, 255, 0.5);
}
.playlist .list-wrapper .list-operate .add .icon-add {
          margin-right: 5px;
          font-size: 10px;
}
.playlist .list-wrapper .list-operate .add .text {
          font-size: 12px;
}
.playlist .list-wrapper .list-close {
      text-align: center;
      line-height: 50px;
      background: #222;
      font-size: 16px;
      color: rgba(255, 255, 255, 0.5);
}



