
.switches {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 240px;
  margin: 0 auto;
  border: 1px solid #333;
  border-radius: 5px;
}
.switches .switch-item {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 8px;
    text-align: center;
    font-size: 15px;
    color: rgba(255, 255, 255, 0.3);
}
.switches .switch-item.active {
      background: #333;
      color: #fff;
}



