
.search-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  padding: 0 6px;
  height: 40px;
  background: #333;
  border-radius: 6px;
}
.search-box .icon-search {
    font-size: 24px;
    color: #222;
}
.search-box .box {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    margin: 0 5px;
    line-height: 18px;
    background: #333;
    color: #fff;
    font-size: 14px;
    border: none;
    outline: none;
}
.search-box .box::-webkit-input-placeholder {
      color: rgba(255, 255, 255, 0.3);
}
.search-box .box:-ms-input-placeholder {
      color: rgba(255, 255, 255, 0.3);
}
.search-box .box::-ms-input-placeholder {
      color: rgba(255, 255, 255, 0.3);
}
.search-box .box::placeholder {
      color: rgba(255, 255, 255, 0.3);
}
.search-box .icon-dismiss {
    font-size: 16px;
    color: #222;
}



