
.search-list .search-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 40px;
  overflow: hidden;
}
.search-list .search-item.list-enter-active, .search-list .search-item.list-leave-active {
    -webkit-transition: all 0.1s;
    transition: all 0.1s;
}
.search-list .search-item.list-enter, .search-list .search-item.list-leave-to {
    height: 0;
}
.search-list .search-item .text {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    color: rgba(255, 255, 255, 0.5);
    font-size: 14px;
}
.search-list .search-item .icon {
    position: relative;
}
.search-list .search-item .icon:before {
      content: '';
      position: absolute;
      top: -10px;
      left: -10px;
      right: -10px;
      bottom: -10px;
}
.search-list .search-item .icon .icon-delete {
      font-size: 12px;
      color: rgba(255, 255, 255, 0.3);
}



