
.suggest {
  height: 100%;
  overflow: hidden;
}
.suggest .suggest-list {
    padding: 0 30px;
}
.suggest .suggest-list .suggest-item {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      padding-bottom: 20px;
}
.suggest .suggest-list .avatar {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 49px;
              flex: 0 0 49px;
      width: 30px;
      margin-left: -8px;
}
.suggest .suggest-list .avatar img {
        border-radius: 30px;
}
.suggest .suggest-list .icon {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 40px;
              flex: 0 0 40px;
      width: 30px;
}
.suggest .suggest-list .icon [class^="icon-"] {
        font-size: 20px;
        color: rgba(255, 255, 255, 0.3);
}
.suggest .suggest-list .name {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      font-size: 15px;
      color: rgba(255, 255, 255, 0.3);
      overflow: hidden;
}
.suggest .suggest-list .name .text {
        font-size: 15px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
}
.suggest .no-result-wrapper {
    position: absolute;
    width: 100%;
    top: 25%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}



